@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.separator {
  border-top: 1px solid #ccc;
  margin-top: 20px;
  margin-bottom: 20px;
}

.file-name-container {
  width: 300px; /* Adjust as needed */
}

.App-logo {
  padding: 20px; /* Adjust the padding as needed */
  position: fixed;
  top: 0;
  left: 0;
  width: 100px; /* Adjust the width as needed */
  height: 90px; /* Adjust the height as needed */
}

.nav-button {
  display: inline-block;
  padding: .05vw 1vw; /* Using viewport units for padding */
  margin-top: 2vh; /* Using viewport units for margin-top */
  font-size: 1vw; /* Using viewport units for font-size */
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  background-color: #fd433c;
  color: white;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}

.prev-button {
  display: inline-block;
  padding: 5px 20px;
  margin-top: 10px;
  font-size: 16px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  background-color: grey;
  color: white;
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
}

.fileupload {
  display: flex;
  align-items: center;
}

.selected-file {
  margin-left: 10px; /* Adjust as needed */
}

a {
  color: blue;
}

.App-page {
  min-height: 100vh;
  display: flex;
  justify-content: space-between;
}

.left-side-page {
  flex: 1;
  background-color: #00234a;
}

img {
  max-width: 100%;
}

.right-side-page {
  flex: 2;
  background-color: white;
  padding: 10px;
  overflow: auto;
}

.right-side-page > div {
  max-width: 800px;
  margin: 0 auto;
}

.text-container {
  padding: 1em; 
  text-align: center;
  display: flex; 
  flex-direction: column;
  align-items: center; 
  justify-content: center; 
  height: 100%; 
}

.text-container img {
  margin-bottom: 1em; 
}

.text-left-main {
  color: white;
  font-size: 30px; 
  margin-top: 1em; 
  padding-left: 20px; 
}

.copy-right {
  color: #808080;
  font-size: .7vw; /* Use relative units for font size */
  margin-bottom: 1.5vh; /* Use relative units for margin bottom */
  position: fixed; /* Ensure it stays at the bottom */
  bottom: 1vh; /* Add some space from the bottom */
  left: 2vw; /* Add some space from the left */
  padding-left: 10vw; /* Use relative units for padding */
  position: fixed;
}

.file-select-button {
  background-color: #fd433c;
  color: #ffffff; 
  padding: 6px 10px; 
  border-radius: 0.275rem;
  cursor: pointer;
  width: 5; 
}

.icon-container {
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px; 
}

.file-upload-input {
  outline: none !important;
}

.App-link {
  color: #61dafb;
}



@media (max-width: 768px) {
  .App-logo {
    height: 20vmin;
  }

  .text-container {
    padding: 0.5em; 
  }

  .text-left-main {
    font-size: 1.5em; 
    margin-top: 0.5em; 
    padding-left: 0; 
  }

  .copy-right {
  font-size: 1vw; /* Adjust font size for smaller screens */
  padding-left: 5vw; /* Adjust padding for smaller screens */
  bottom: 2vh; /* Adjust bottom space for smaller screens */
  left: 5vw; /* Adjust left space for smaller screens */
  position: absolute;
  margin: 0; /* Remove margin to prevent movement */
}

  .App-page {
    flex-direction: column; /* Stack the elements vertically on smaller screens */
  }

  /* Specific changes for right-side-page */
  .right-side-page {
    padding: 5px;
  }

  .right-side-page > div {
    max-width: 100%;
    padding: 10px;
  }
}

/* Media query for screens larger than 1200px */
@media (min-width: 1200px) {
  .right-side-page {
    padding: 20px;
  }

  .right-side-page > div {
    max-width: 1000px;
  }
}

